import React, { Component, Fragment } from "react";
import { Redirect } from "react-router-dom";
import ProductsList from "./ProductsList";
import Navigator from "./Navigator";

export default class Home extends Component {
    state = {
        redirect: true,
    };

    constructor() {
        super();

        if (sessionStorage.getItem("token")) {
            this.state = { redirect: false };
        }
    }

    componentDidMount() {
        document.body.className = "bg-seguros";
    }

    render() {
        if (this.state.redirect) {
            return <Redirect to={"/login"}></Redirect>;
        }
        return (
            <Fragment>
                <Navigator />
                <div className="row vh-100 justify-content-center align-items-center">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-md-8 mx-auto">
                                <div className="card border-0">
                                    <div className="card-header mb-2 px-0 pt-3">
                                        <h3 className="text-center py-3 my-2">Bienvenid@</h3>
                                        <p className="text-center text-dark fontS-normal">Selecciona la póliza de seguro que deseas adquirir</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row capa">
                            <div className="col-md-10 mx-auto">
                                <div className="row align-items-center justify-content-center">
                                    <ProductsList></ProductsList>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
