import React, { Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
export default class ProductsList extends Component {
    state = {
        error: null,
        data: undefined,
        loading: true,
    };

    componentDidMount() {
        this.fetchData();
    }

    fetchData = async () => {
        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token"),
            };
            let response = await axios.get(process.env.REACT_APP_SIMPLI_API_HOST + "/products", {
                headers,
            });
            let { data } = response.data;
            this.setState({ loading: false, data });
        } catch (error) {
            this.setState({ loading: false, error });
        }
    };

    render() {
        if (this.state.loading) {
            return (
                <div>
                    <div className="spinner-grow text-primary" role="status" style={{ width: 6 + "rem", height: 6 + "rem" }}>
                        <span className="sr-only">Loading...</span>
                    </div>
                </div>
            );
        }
        return this.state.data.map((product) => {
            return (
                <div className="col-6 col-md-3 my-3" key={product.id}>
                    <figure className="my-auto p-2">
                        <h5 className="text-center text-white">{product.name}</h5>
                        <img
                            className="img-fluid d-block rounded-circle border-3 border-primary bg-white mx-auto my-3"
                            src={product.image_url}
                            alt={product.name}
                        />

                        <Link to={"quotation/" + product.id} className="btn btn-primary d-table mx-auto">
                            Cotiza y Compra
                        </Link>
                    </figure>
                </div>
            );
        });
    }
}
