import React, { Component } from "react";
import { Link } from "react-router-dom";
import Check from "../imagenes/check.png";
import axios from "axios";

export default class Payment extends Component {
    state = {
        loading: true,
        error: null,
    };

    componentDidMount() {
        this.tryPayment();
    }

    tryPayment = async (e) => {
        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token"),
            };
            let response = await axios.post(process.env.REACT_APP_SIMPLI_API_HOST + "/payments/pay/", this.props.invoice.id, {
                headers,
            });
            let { data } = response.data;
            this.setState({ loading: false });
        } catch (error) {
            this.setState({ loading: false, error });
        }
    };

    render() {
        if (this.state.loading) {
            return (
                <div className="row justify-content-center align-items-center vh-100">
                    <div className="spinner-grow text-primary" style={{ width: 6 + "rem", height: 6 + "rem" }} role="status">
                        <span className="sr-only">Loading...</span>
                    </div>
                </div>
            );
        }
        return (
            <div className="row justify-content-center align-items-center vh-100">
                <div className="col-5">
                    <div className="card" style={{ background: "#FFF" }}>
                        <div className="row justify-content-center">
                            <div className="col-7 text-center">
                                <img width={130} src={Check} alt="" />
                                <h4 className="mt-4" style={{ color: "#fbb03b" }}>
                                    PAGO EXITOSO
                                </h4>
                                <p className="mb-5">Tu póliza se ha enviado a la bandeja de entrada de tu correo electrónico</p>
                                <Link to="/" className="btn btn-primary btn-block mb-4">
                                    Continuar
                                </Link>
                                <a className="d-block mb-4" href="https://simplidigital.co/soat.pdf" target="_blank" rel="noopener noreferrer">
                                    Ver mi póliza
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
