import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import axios from "axios";

export default class Logout extends Component {
    state = {
        loggedIn: true,
    };
    componentDidMount() {
        if (!sessionStorage.getItem("token")) {
            return false;
        }

        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token"),
            };
            axios.post(
                process.env.REACT_APP_SIMPLI_API_HOST + "/logout",
                {},
                {
                    headers,
                }
            );
            sessionStorage.removeItem("token");
            window.location.href = process.env.REACT_APP_SIMPLI_AUTH_HOST + "/logout?redirect=" + document.location.origin + "%2Fapp";
            this.setState({ loggedIn: false });
        } catch (error) {
            this.setState({ loading: false, error });
        }
    }

    render() {
        if (this.state.loggedIn) {
            return (
                <div className="row justify-content-center align-items-center vh-100">
                    <div className="spinner-grow text-primary" style={{ width: 6 + "rem", height: 6 + "rem" }} role="status">
                        <span className="sr-only">Loading...</span>
                    </div>
                </div>
            );
        } else {
            return <Redirect to={"/"}></Redirect>;
        }
    }
}
