import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import Issue from "./Issue";
import Modal from "./Modal";
import Form from "./Form";
import Controls from "./Controls";

export default class Quotation extends Component {
    state = {
        error: null,
        data: undefined,
        loading: true,
        form: {
            product_id: this.props.match.params.id,
        },
        quotation: undefined,
        isOpen: false,
        panelBg: null,
        secondStep: undefined,
    };

    componentDidMount() {
        this.changeBackground();
        this.fetchData();
    }

    fetchData = async () => {
        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token"),
            };
            let response = await axios.get(process.env.REACT_APP_SIMPLI_API_HOST + "/products/" + this.props.match.params.id, {
                headers,
            });
            let { data } = response.data;
            this.setState({ loading: false, data });
        } catch (error) {
            this.setState({ loading: false, error });
        }
    };

    parseQuotation = (quotation) => {
        let details = {};
        quotation.details.map((detail) => {
            details[detail.product_field.alias] = detail.value;
            return true;
        });
        quotation.details = details;
        return quotation;
    };

    handleSubmit = async (e) => {
        e.preventDefault();

        this.setState({ loading: true, error: null });

        if (this.state.form.product_id !== "b217e2e3-f354-4938-a768-c2a825add357") {
            this.setState({ loading: false, secondStep: true });
            return true;
        }

        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token"),
            };
            let response = await axios.post(process.env.REACT_APP_SIMPLI_API_HOST + "/quotations", this.state.form, {
                headers,
            });
            let { data } = response.data;
            data = this.parseQuotation(data);
            this.setState({ loading: false, quotation: data });
        } catch (error) {
            this.setState({ loading: false, error });
        }
    };

    handleChange = (e) => {
        var form = { ...this.state.form };
        form[e.target.name] = e.target.value;
        this.setState({ form });
    };

    openModal = () => {
        this.setState({ isOpen: true });
    };

    closeModal = (e) => {
        e.preventDefault();
        this.setState({ isOpen: false });
    };

    changeBackground = () => {
        let panelBg = "";
        if (this.state.form.product_id === "b217e2e3-f354-4938-a768-c2a825add357") {
            document.body.className = "bg-soat";
            panelBg = "bg-soat-celular";
        } else if (this.state.form.product_id === "1fa1e938-2728-4d0d-82fc-135652b04c5b") {
            document.body.className = "bg-Vivienda-Segura";
            panelBg = "bg-familia";
        } else if (this.state.form.product_id === "4012ca45-fa26-4c38-8946-41adef9bfe53") {
            document.body.className = "bg-Rce-Tipo";
            panelBg = "bg-Rce-llamada";
        }
        this.setState({ panelBg });
    };

    goBack = (e) => {
        e.preventDefault();
        this.props.history.goBack();
    };

    render() {
        if (this.state.error) {
            return (
                <div className="row justify-content-center align-items-center vh-100">
                    <div className="col-md-6">
                        <div className="card border-0">
                            <div className="card-header mb-2 px-0 pt-3">
                                <h3 className="text-center py-3 my-2">ALGO VA MAL</h3>
                            </div>
                            <div className="card-body text-center">
                                <p>{this.state.error.response.data.message}</p>
                                <Link to="/" className="btn btn-primary mt-2">
                                    Continuar
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            );
        }

        if (this.state.loading) {
            return (
                <div className="row justify-content-center align-items-center vh-100">
                    <div className="spinner-grow text-primary" style={{ width: 6 + "rem", height: 6 + "rem" }} role="status">
                        <span className="sr-only">Loading...</span>
                    </div>
                </div>
            );
        }

        if (this.state.secondStep) {
            return (
                <Form product={this.state.data} form={this.state.form} goBack={this.goBack} handleChange={this.handleChange}>
                    <Controls product={this.state.data} />
                </Form>
            );
        }

        if (this.state.quotation) {
            return <Issue goBack={this.goBack} product={this.state.data} quotation={this.state.quotation}></Issue>;
        }

        return (
            <div className="row vh-100 justify-content-center align-items-center">
                <div className="col-11 col-md-9">
                    <div className="card border-0">
                        <div className="card-header mb-2 px-0 pt-3">
                            <h3 className="text-center py-3 my-2">ADQUIERE TU SEGURO {this.state.data.name}</h3>
                        </div>
                        <div className="card-body px-3 py-0">
                            <div className="row">
                                <div className="col-12 col-md-6">
                                    <a href="/#" onClick={this.goBack} className="d-inline-block ml-3 mt-2">
                                        &#10094; Atrás
                                    </a>
                                    <form className="py-5" onSubmit={this.handleSubmit}>
                                        <img
                                            className="d-block mx-auto rounded-circle shadow-sm"
                                            src={this.state.data.image_url}
                                            alt={this.state.data.name}
                                        />

                                        {this.state.form.product_id === "b217e2e3-f354-4938-a768-c2a825add357" ? (
                                            <input
                                                className="form-control mx-auto mt-5 w-50 p-4 text-center text-uppercase"
                                                name="plate"
                                                placeholder="ingresa tu placa"
                                                type="text"
                                                required
                                                onChange={this.handleChange}
                                            />
                                        ) : this.state.form.product_id === "4012ca45-fa26-4c38-8946-41adef9bfe53" ? (
                                            <select
                                                className="custom-select select-productos w-50 d-block mx-auto mb-4 mt-5"
                                                defaultValue="Tipo de producto"
                                                name="type"
                                                onChange={this.handleChange}
                                                required
                                            >
                                                <option disabled>Tipo de producto</option>
                                                <option value="heavy">Pesados</option>
                                                <option value="lightweight">Livianos</option>
                                                <option value="motorbike">Motos</option>
                                                <option value="excess">Exceso</option>
                                            </select>
                                        ) : (
                                            <div className="mt-5"></div>
                                        )}
                                        <button className="btn btn-primary btn-block mt-3 mx-auto w-50 p-2" type="submit">
                                            Continuar
                                        </button>
                                    </form>
                                </div>
                                <div
                                    style={{ overflow: "hidden" }}
                                    className={"col-12 col-md-6 d-md-flex flex-column p-5 rounded-left-card " + this.state.panelBg}
                                >
                                    <button onClick={this.openModal} className="btn btn-secondary mt-auto mx-auto w-75 p-2">
                                        ¿Qué es un seguro {this.state.data.name}?
                                    </button>
                                    <Modal
                                        isOpen={this.state.isOpen}
                                        handleClose={this.closeModal}
                                        title={"¿Qué es un seguro " + this.state.data.name + "?"}
                                        bgClass={
                                            this.state.form.product_id === "1fa1e938-2728-4d0d-82fc-135652b04c5b"
                                                ? "bg-info-vivienda"
                                                : "bg-info-soat"
                                        }
                                    ></Modal>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
