import React, { Component } from "react";
import DOMElement from "./DOMElement";
import axios from "axios";
import payform from "payform";
import Cleave from "cleave.js/react";
import Car from "../Iconos/dbd77eed18346f9d88cb4596e9a354b7.svg";
import RCE from "../Iconos/47e00c63f3adb248f752c944151cd18b.svg";
import Hogar from "../Iconos/e00b651c308249036ccf8e3809da2e61.svg";
import Pencil from "../Iconos/6adcfbea0118a9c4c4f377a660d8b31f.svg";
import Payment from "./Payment";
import External from "./External";
import PaymentsCC from "../Logos/3f39ca4aeae4b204c21f8dacbba2f391.png";
import PaymentsPSE from "../Logos/b24f90b0582b08f1548b942477bb45c7.png";
import PaymentsEB from "../Logos/1ab9582283f6859d7b3a801c9667f8f8.png";
import PaymentsCB from "../Logos/7d38e806776f7f2be699625eadb13a65.png";
import { Link } from "react-router-dom";

export default class Issue extends Component {
    state = {
        error: null,
        loading: true,
        data: undefined,
        payment_methods: undefined,
        form: {
            quotation_id: this.props.quotation.id,
        },
        external_site: undefined,
        credit_card: undefined,
        department: null,
        readonly: undefined,
        validation: true,
        redirect: null,
        issue: undefined,
    };

    componentDidMount() {
        this.fetchUserData();
        if (!this.props.product.configuration.payment_url) {
            this.pingPaymentService();
            this.fetchPaymentMethods();
        }
    }

    fetchPaymentMethods = async () => {
        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token"),
            };
            let response = await axios.get(process.env.REACT_APP_SIMPLI_API_HOST + "/payments/methods", {
                headers,
            });
            let { data } = response.data;
            this.setState({ loading: false, payment_methods: data });
        } catch (error) {
            this.setState({ loading: false, error });
        }
    };

    validateCC = () => {
        if (this.state.credit_card.number && this.state.credit_card.cvv && this.state.credit_card.good_thru) {
            if (this.state.credit_card.good_thru.includes("/")) {
                let date = this.state.credit_card.good_thru.split("/");
                let validationCVV = payform.validateCardCVC(this.state.credit_card.cvv);
                let validationCN = payform.validateCardNumber(this.state.credit_card.number);
                let type = payform.parseCardType(this.state.credit_card.number);
                let validationCE = payform.validateCardExpiry(date[1], date[0]);
                let available =
                    type &&
                    this.state.payment_methods["credit"].some((el) => {
                        return el.description === type.toUpperCase();
                    });
                if (validationCE && validationCN && validationCVV && available) {
                    this.setState({ validation: true });
                } else {
                    this.setState({ validation: false });
                }
            } else {
                this.setState({ validation: false });
            }
        }
    };

    handleCC = async (e) => {
        let credit_card = { ...this.state.credit_card };
        credit_card[e.target.name] = e.target.name === "number" ? e.target.rawValue : e.target.value;
        await this.setState({ credit_card });
        this.validateCC();
    };

    pingPaymentService = async () => {
        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token"),
            };
            await axios.post(process.env.REACT_APP_SIMPLI_API_HOST + "/payments/ping", null, {
                headers,
            });
            this.setState({ loading: false });
        } catch (error) {
            this.setState({ loading: false, error });
        }
    };

    fetchUserData = async () => {
        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token"),
            };
            let response = await axios.get(process.env.REACT_APP_SIMPLI_API_HOST + "/me", {
                headers,
            });
            let { data } = response.data;
            let form = { ...this.state.form };
            form = Object.assign(form, this.parseData(data));
            let readonly = Object.keys(data);
            this.setState({ loading: false, data, form, readonly });
        } catch (error) {
            this.setState({ loading: false, error });
        }
    };

    parseData = (data) => {
        let parsed = {};
        for (const e in data) {
            parsed[e] = typeof data[e] === "object" && data[e] !== null ? data[e].code : data[e];
        }
        return parsed;
    };

    handleChange = (e) => {
        var form = { ...this.state.form };
        form[e.target.name] = e.target.value;
        this.setState({ form });
        if (e.target.name === "residence_department") {
            this.setState({ department: e.target.value });
        }
    };

    handleDepartment = (e) => {
        this.setState({ department: null });
    };

    handleClick = (e) => {
        this.setState({ readonly: [] });
    };

    handleSubmit = async (e) => {
        e.preventDefault();

        this.setState({ loading: true, error: null });

        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token"),
            };
            let response = await axios.post(process.env.REACT_APP_SIMPLI_API_HOST + "/issues", this.state.form, {
                headers,
            });
            let { data } = response.data;
            let redirect = true;
            let external_site = undefined;
            if (data.payment_url) {
                redirect = false;
                external_site = data.payment_url;
            }
            this.setState({
                loading: false,
                issue: data,
                redirect,
                external_site,
            });
        } catch (error) {
            console.log(error);
            console.log(error.response.data);
            this.setState({ loading: false, error });
        }
    };

    render() {
        if (this.state.redirect) {
            return <Payment invoice={this.state.issue.invoice} />;
        }
        if (this.state.external_site) {
            return <External site={this.state.external_site} />;
        }
        if (this.state.error) {
            return (
                <div className="row justify-content-center align-items-center vh-100">
                    <div className="col-md-6">
                        <div className="card border-0">
                            <div className="card-header mb-2 px-0 pt-3">
                                <h3 className="text-center py-3 my-2">ALGO VA MAL</h3>
                            </div>
                            <div className="card-body text-center">
                                <p>{this.state.error.response.data.message}</p>
                                <Link to="/" className="btn btn-primary mt-2">
                                    Continuar
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            );
        }
        if (this.state.loading) {
            return (
                <div className="row justify-content-center align-items-center vh-100">
                    <div className="spinner-grow text-primary" style={{ width: 6 + "rem", height: 6 + "rem" }} role="status">
                        <span className="sr-only">Loading...</span>
                    </div>
                </div>
            );
        }
        return (
            <div className="row vh-100 justify-content-center align-items-center">
                <div className="col-11 col-md-8">
                    <div className="card border-0">
                        <div className="card-header mb-2 px-0 pt-3">
                            <h3 className="text-center py-3 my-2">ADQUIERE TU SEGURO {this.props.product.name}</h3>
                        </div>
                        <form onSubmit={this.handleSubmit}>
                            <div className="card-body px-3 py-0">
                                <div className="row">
                                    <div className="col-12 col-md-6 pt-3">
                                        <a href="/#" onClick={this.props.goBack}>
                                            &#10094; Atrás
                                        </a>
                                        {this.props.product.id === "b217e2e3-f354-4938-a768-c2a825add357" ? (
                                            <div className="mx-auto px-md-4 mb-3">
                                                <p className="text-center">
                                                    No te preocupes, si deseas corregir un dato, da click al ícono del lapicero
                                                </p>
                                                <div className="col-md-12 d-flex justify-content-end">
                                                    <button className="btn btn-link btn-xs mb-2 p-0" type="button" onClick={this.handleClick}>
                                                        <img
                                                            style={{
                                                                width: "20px",
                                                            }}
                                                            src={Pencil}
                                                            alt=""
                                                        />
                                                    </button>
                                                </div>
                                                <div className="col-12">
                                                    <div className="row">
                                                        {this.props.product.fields.map((field) => {
                                                            if (!field.required_quotation) {
                                                                return (
                                                                    <DOMElement
                                                                        field={field}
                                                                        key={field.alias}
                                                                        wrap={
                                                                            field.alias.includes("address") ? "form-group col-12" : "form-group col-6"
                                                                        }
                                                                        onChangeInput={this.handleChange}
                                                                        department={this.state.department}
                                                                        handleDepartment={this.handleDepartment}
                                                                        user={this.state.data}
                                                                        read={
                                                                            this.state.readonly.includes(field.alias) &&
                                                                            this.state.data[field.alias] &&
                                                                            true
                                                                        }
                                                                    ></DOMElement>
                                                                );
                                                            }

                                                            return "";
                                                        })}{" "}
                                                    </div>
                                                </div>
                                            </div>
                                        ) : null}
                                    </div>
                                    <div className="col-12 col-md-6 pt-3 px-3 Pagos overflow-auto rounded-left-card">
                                        <div className="row">
                                            <div className="d-none col-3 border-right border-primary d-md-flex ml-auto imagen-resumen shadow-sm">
                                                {this.props.product.id === "1fa1e938-2728-4d0d-82fc-135652b04c5b" ? (
                                                    <img className="img-fluid d-block mx-auto" src={Hogar} alt="" />
                                                ) : this.props.product.id === "b217e2e3-f354-4938-a768-c2a825add357" ? (
                                                    <img className="img-fluid d-block mx-auto" src={Car} alt="" />
                                                ) : this.props.product.id === "4012ca45-fa26-4c38-8946-41adef9bfe53" ? (
                                                    <img className="img-fluid d-block mx-auto" src={RCE} alt="" />
                                                ) : null}
                                            </div>
                                            <div className="card resumen-pago p-0 col-md-8 mr-auto border-0 shadow-sm">
                                                <div className="card-header text-center p-0 border-bottom border-primary mb-0">
                                                    <p className="my-auto m-0 p-1">COSTO DE TU SEGURO {this.props.product.name}</p>
                                                </div>
                                                <div className="card-body text-center py-0">
                                                    <div className="row justify-content-center">
                                                        {this.props.product.id === "1fa1e938-2728-4d0d-82fc-135652b04c5b" ? null : (
                                                            <div className="col-6 text-center">
                                                                <p className="mb-0">Vehículo</p>
                                                                <p className="font-weight-bold mb-2">
                                                                    {this.props.quotation.details.vehicle_brand +
                                                                        " " +
                                                                        this.props.quotation.details.vehicle_line}
                                                                </p>
                                                                <p className="mb-0">Placas</p>
                                                                <p className="font-weight-bold mb-1">{this.props.quotation.details.plate}</p>
                                                            </div>
                                                        )}
                                                        <div className="col-md-6 my-auto Valor">
                                                            <p className="m-0">Valor Total</p>
                                                            <span>{this.props.quotation.amount.toLocaleString()}</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        {this.props.product.configuration.payment_url ? null : (
                                            <div className="mx-auto medios-de-pago py-2">
                                                <h2 className="text-center my-3">MEDIOS DE PAGO</h2>
                                                <div className="card mx-auto border-0 px-4">
                                                    <ul className="list-group list-group-flush shadow">
                                                        <li className="list-group-item p-0">
                                                            <input type="radio" name="payment_method" className="d-none" id="PSE" />
                                                            <label className="my-auto ml-2 px-2 py-2 w-100" htmlFor="PSE">
                                                                Pago por PSE
                                                                <img src={PaymentsPSE} alt="" />
                                                            </label>
                                                            <div className="form-PSE none form-pagos">
                                                                <div className="row px-4 py-2 justify-content-center">
                                                                    <p className="my-0">This method is not available at this moment.</p>
                                                                </div>
                                                            </div>
                                                        </li>
                                                        <li className="list-group-item p-0">
                                                            <input type="radio" name="payment_method" className="d-none" id="Tarje_Credito" />
                                                            <label className="my-auto ml-2 px-2 py-2 w-100" htmlFor="Tarje_Credito">
                                                                Tarjeta de Crédito
                                                                <img src={PaymentsCC} alt="" />
                                                            </label>
                                                            <div className="form-Tarjeta none form-pagos">
                                                                <div className="row px-4 py-2">
                                                                    <div className="col-12 mb-2">
                                                                        <Cleave
                                                                            required={true}
                                                                            placeholder="0000 0000 0000 0000"
                                                                            name="number"
                                                                            options={{
                                                                                creditCard: true,
                                                                            }}
                                                                            className={
                                                                                this.state.validation ? "form-control" : "form-control is-invalid"
                                                                            }
                                                                            onChange={this.handleCC}
                                                                        />
                                                                        {!this.state.validation && (
                                                                            <div className="invalid-feedback">Tarjeta no válida</div>
                                                                        )}
                                                                    </div>
                                                                    <div className="col-md-4">
                                                                        <Cleave
                                                                            required={true}
                                                                            name="cvv"
                                                                            placeholder="CVV"
                                                                            options={{
                                                                                numeral: true,
                                                                            }}
                                                                            className={
                                                                                this.state.validation ? "form-control" : "form-control is-invalid"
                                                                            }
                                                                            onChange={this.handleCC}
                                                                        ></Cleave>
                                                                    </div>
                                                                    <div className="col-md-4">
                                                                        <Cleave
                                                                            required={true}
                                                                            name="good_thru"
                                                                            placeholder="YYYY/MM"
                                                                            options={{
                                                                                date: true,
                                                                                datePattern: ["Y", "m"],
                                                                            }}
                                                                            className={
                                                                                this.state.validation ? "form-control" : "form-control is-invalid"
                                                                            }
                                                                            onChange={this.handleCC}
                                                                        ></Cleave>
                                                                    </div>
                                                                    <div className="col-md-4">
                                                                        <Cleave
                                                                            required={true}
                                                                            name="quota"
                                                                            placeholder="Cuotas"
                                                                            options={{
                                                                                numeral: true,
                                                                            }}
                                                                            className="form-control"
                                                                            onChange={this.handleChange}
                                                                        ></Cleave>
                                                                    </div>
                                                                    <div className="col-12">
                                                                        <small>* Cuotas soportadas para Codensa: 6, 12, 18, 24, 36 y 48.</small>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </li>
                                                        <li className="list-group-item p-0">
                                                            <input type="radio" name="payment_method" className="d-none" id="Efecty_Baloto" />
                                                            <label className="my-auto ml-2 px-2 py-2 w-100" htmlFor="Efecty_Baloto">
                                                                Efecty o Baloto
                                                                <img src={PaymentsEB} alt="" />
                                                            </label>
                                                            <div className="form-Efecty-Baloto none form-pagos">
                                                                <div className="row px-4 py-2 justify-content-center">
                                                                    <p className="my-0">This method is not available at this moment.</p>
                                                                </div>
                                                            </div>
                                                        </li>
                                                        <li className="list-group-item p-0">
                                                            <input type="radio" name="payment_method" className="d-none" id="Consignación" />
                                                            <label className="my-auto ml-2 px-2 py-2 w-100" htmlFor="Consignación">
                                                                Consignación bancaria
                                                                <img src={PaymentsCB} alt="" />
                                                            </label>
                                                            <div className="form-Consignacion none form-pagos">
                                                                <div className="row px-4 py-2 justify-content-center">
                                                                    <p className="my-0">This method is not available at this moment.</p>
                                                                </div>
                                                            </div>
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>
                                        )}
                                        {this.props.quotation.discount ? (
                                            <p>Valor descuento: ${this.props.quotation.discount.toLocaleString()}</p>
                                        ) : null}
                                        <button className="btn btn-primary btn-lg w-100 d-block mx-auto my-3">
                                            {"Pagar ahora $(" + this.props.quotation.amount.toLocaleString() + ")"}
                                        </button>
                                        <p>
                                            Su póliza será expedida con
                                            <img
                                                className="ml-2"
                                                style={{ width: "80px" }}
                                                alt={`Logo ${this.props.product.insurers[0].name}`}
                                                src={this.props.product.insurers[0].logo_url}
                                            />
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}
