import React, { Component } from "react";
import axios from "axios";
import Address from "./Address";

export default class DOMElement extends Component {
    constructor(props) {
        super(props);

        let DOMElement = this.props.field.dom_element_type;
        let type = "text";
        if (this.props.field.dom_element_type.includes(":")) {
            type = DOMElement.split(":")[1];
            DOMElement = DOMElement.split(":")[0];
        }
        this.state = {
            DOMElement,
            type,
            options: null,
        };
    }

    componentDidMount() {
        this.fetchOptions(this.props.field.alias);
    }

    componentDidUpdate() {
        if (this.props.field.alias === "residence_city" && this.props.department) {
            this.fetchNewOptions(this.props.field.alias);
        }
    }

    fetchNewOptions = async (alias) => {
        if (alias === "residence_city" && this.props.department) {
            let options = [];
            let department = this.props.department;
            this.props.handleDepartment();
            this.setState({ fetching: true });
            try {
                let headers = {
                    Authorization: "Bearer " + sessionStorage.getItem("token"),
                };
                let response = await axios.get(process.env.REACT_APP_SIMPLI_API_HOST + "/utils/departments/" + department + "/cities", {
                    headers,
                });
                let { data } = response.data;
                options = data;
            } catch (error) {
                console.error("Could not get any data");
            }
            this.setState({ options, fetching: false });
        }
    };

    fetchOptions = async (alias) => {
        let options = [];
        if (alias === "economic_activity") {
            try {
                let headers = {
                    Authorization: "Bearer " + sessionStorage.getItem("token"),
                };
                let response = await axios.get(process.env.REACT_APP_SIMPLI_API_HOST + "/utils/economic_activities", {
                    headers,
                });
                let { data } = response.data;
                options = data;
            } catch (error) {
                console.error("Could not get any data");
            }
        }
        if (alias === "residence_department") {
            try {
                let headers = {
                    Authorization: "Bearer " + sessionStorage.getItem("token"),
                };
                let response = await axios.get(process.env.REACT_APP_SIMPLI_API_HOST + "/utils/departments", {
                    headers,
                });
                let { data } = response.data;
                options = data;
            } catch (error) {
                console.error("Could not get any data");
            }
        }
        if (alias === "document_type") {
            try {
                let headers = {
                    Authorization: "Bearer " + sessionStorage.getItem("token"),
                };
                let response = await axios.get(process.env.REACT_APP_SIMPLI_API_HOST + "/utils/document_types", {
                    headers,
                });
                let { data } = response.data;
                options = data;
            } catch (error) {
                console.error("Could not get any data");
            }
        }
        this.setState({ options, fetching: false });
    };

    handleFocus = (e) => {
        e.currentTarget.type = "date";
    };

    render() {
        if (this.state.fetching) {
            return (
                <div className="col-4 text-center">
                    <div className="spinner-grow text-primary" role="status">
                        <span className="sr-only">Loading...</span>
                    </div>
                </div>
            );
        }
        if (this.state.DOMElement === "input") {
            if (this.props.field.alias.includes("address")) {
                return (
                    <div className={this.props.wrap ? this.props.wrap : "form-group"}>
                        <Address onChange={this.props.onChangeInput} name={this.props.field.alias} />
                    </div>
                );
            } else {
                return (
                    <div className={this.props.wrap ? this.props.wrap : "form-group"}>
                        <input
                            type={this.state.type === "date" ? "text" : this.state.type}
                            className={this.props.className ? "form-control " + this.props.className : "form-control"}
                            name={this.props.field.alias}
                            placeholder={this.props.field.label}
                            onChange={this.props.onChangeInput}
                            required={this.props.field.required}
                            onFocus={this.state.type === "date" ? this.handleFocus : null}
                            defaultValue={
                                this.props.user
                                    ? this.props.user[this.props.field.alias]
                                        ? this.props.user[this.props.field.alias]
                                        : undefined
                                    : undefined
                            }
                            disabled={this.props.read && true}
                        />
                    </div>
                );
            }
        } else if (this.state.DOMElement === "select") {
            return (
                <div className={this.props.wrap ? this.props.wrap : "form-group"}>
                    <select
                        name={this.props.field.alias}
                        required={this.props.field.required}
                        onChange={this.props.onChangeInput}
                        className="form-control"
                        defaultValue={this.props.field.label}
                        disabled={this.props.read && true}
                    >
                        <option disabled>{this.props.field.label}</option>
                        {this.state.options &&
                            this.state.options.map((option) => {
                                return (
                                    <option key={option.code} value={option.code}>
                                        {option.description}
                                    </option>
                                );
                            })}
                    </select>
                </div>
            );
        }
    }
}
