import React, { Component } from "react";

export default class Address extends Component {
    state = {
        road_type: "",
        road_number: "",
        subroad: "",
        subroad_complement: "",
    };

    handleChange = (e) => {
        let state = { ...this.state };
        state[e.target.name] = e.target.value;
        let value = `${state.road_type} ${state.road_number} # ${state.subroad}-${state.subroad_complement}`;
        this.props.onChange({ target: { name: this.props.name, value } });
        this.setState(state);
    };

    render() {
        return (
            <div className="d-flex align-items-center">
                <div className="flex-fill">
                    <select className="form-control" name="road_type" onChange={this.handleChange} value={this.state.road_type}>
                        <option value="" disabled>
                            Seleccione una opción...
                        </option>
                        <option value="CL">Calle</option>
                        <option value="DG">Diagonal</option>
                        <option value="TV">Transversal</option>
                        <option value="CR">Carrera</option>
                    </select>
                </div>
                <div className="flex-fill ml-2">
                    <input name="road_number" onChange={this.handleChange} value={this.state.road_number} type="text" className="form-control" />
                </div>
                <div className="flex-fill mx-1">
                    <span>#</span>
                </div>
                <div className="flex-fill">
                    <input name="subroad" onChange={this.handleChange} value={this.state.subroad} type="text" className="form-control" />
                </div>
                <div className="flex-fill">
                    <span>-</span>
                </div>
                <div className="flex-fill">
                    <input
                        name="subroad_complement"
                        onChange={this.handleChange}
                        value={this.state.subroad_complement}
                        type="text"
                        className="form-control"
                    />
                </div>
            </div>
        );
    }
}
