import React, { Component } from "react";
import { Redirect } from "react-router-dom";

export default class Login extends Component {
    state = {
        redirect: false,
    };

    constructor() {
        super();

        if (sessionStorage.getItem("token")) {
            this.setState({ redirect: true });
        }
    }

    handleSubmit = (e) => {
        e.preventDefault();
        window.location.href =
            process.env.REACT_APP_SIMPLI_AUTH_HOST +
            "/oauth/authorize?client_id=" +
            process.env.REACT_APP_SIMPLI_API_CLIENT +
            "&redirect_uri=" +
            process.env.REACT_APP_CALLBACK_HOST +
            "&response_type=token&state=QWIOOP776vgu6868";
    };

    render() {
        if (this.state.redirect) {
            return <Redirect to={"/"}></Redirect>;
        }
        return (
            <div className="row justify-content-center align-items-center vh-100">
                <div className="col-md-3">
                    <div className="card border-0">
                        <div className="card-header mb-2 px-0 pt-3">
                            <h3 className="text-center py-3 my-2">INICIAR SESIÓN</h3>
                        </div>
                        <div className="card-body text-center">
                            <form onSubmit={this.handleSubmit}>
                                <button className="btn btn-primary" type="submit">
                                    Compra aquí
                                </button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
