import React, { Component } from "react";
import axios from "axios";
import Issue from "./Issue";

export default class Form extends Component {
    state = {
        quotation: undefined,
        form: this.props.form,
        next: false
    };

    handleSubmit = async e => {
        e.preventDefault();

        if (this.state.quotation) {
            this.setState({ next: true });
            return true;
        }

        this.setState({ loading: true, error: null });

        try {
            let headers = {
                Authorization: "Bearer " + sessionStorage.getItem("token")
            };
            let response = await axios.post(process.env.REACT_APP_SIMPLI_API_HOST + "/quotations", this.state.form, {
                headers
            });
            let { data } = response.data;
            this.setState({ loading: false, quotation: data });
        } catch (error) {
            this.setState({ loading: false, error });
        }
    };

    handleChange = e => {
        var form = { ...this.state.form };
        form[e.target.name] = e.target.value;
        this.setState({ form });
    };

    render() {
        if (this.state.next) {
            return <Issue goBack={this.props.goBack} product={this.props.product} quotation={this.state.quotation}></Issue>;
        }

        return (
            <div className="row vh-100 align-items-center justify-content-center">
                <div className="col-9">
                    <div className="card border-0">
                        <div className="card-header mb-2 px-0 pt-3">
                            <h3 className="text-center py-3 my-2">ADQUIERE TU SEGURO {this.props.product.name}</h3>
                            <p className="my-2 text-center lead text-dark">
                                {"Completa el formulario para adquirir tu seguro " + this.props.product.name}
                            </p>
                        </div>
                        <div className="card-body p-0">
                            <a href="/#" onClick={this.props.goBack} className="d-inline-block ml-3 mt-2">
                                &#10094; Atrás
                            </a>
                            <div className="formulario-Rce overflow-auto">
                                <form className="mx-auto py-4 px-0 overflowX" onSubmit={this.handleSubmit}>
                                    {React.cloneElement(this.props.children, {
                                        handleChange: this.handleChange
                                    })}
                                    {this.state.quotation ? (
                                        <React.Fragment>
                                            <hr className="hr-form mx-5 mt-5" />
                                            <div className="form-row mt-4 px-5">
                                                {this.state.form.product_id === "4012ca45-fa26-4c38-8946-41adef9bfe53" && (
                                                    <React.Fragment>
                                                        <div className="col-4 my-2">
                                                            <div className="row">
                                                                <div className="col-4 mr-0 pr-0 mt-1">
                                                                    <p className="font-weight-bold line-height-small text-center">Prima bruta:</p>
                                                                </div>
                                                                <div className="col-8 pl-0 ml-0">
                                                                    <input
                                                                        value={this.state.quotation.details.premium}
                                                                        className="form-control"
                                                                        type="text"
                                                                        readOnly
                                                                        placeholder="$"
                                                                    />
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="col-4 order-3 my-2">
                                                            <div className="row">
                                                                <div className="col-4 mr-0 pr-0 mt-1">
                                                                    <p className="font-weight-bold line-height-small text-center">Descuento:</p>
                                                                </div>
                                                                <div className="col-8 pl-0 ml-0">
                                                                    <input
                                                                        value={this.state.quotation.details.discount}
                                                                        className="form-control"
                                                                        type="text"
                                                                        readOnly
                                                                        placeholder="$"
                                                                    />
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="col-4 order-4 my-2">
                                                            <div className="row">
                                                                <div className="col-4 mr-0 pr-0 mt-1">
                                                                    <p className="font-weight-bold line-height-small text-center">
                                                                        Gastos de expedición:
                                                                    </p>
                                                                </div>
                                                                <div className="col-8 pl-0 ml-0">
                                                                    <input
                                                                        value={this.state.quotation.details.costs}
                                                                        className="form-control"
                                                                        type="text"
                                                                        readOnly
                                                                        placeholder="$"
                                                                    />
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </React.Fragment>
                                                )}
                                                <div className="col-4 my-2">
                                                    <div className="row">
                                                        <div className="col-4 mr-0 pr-0 mt-1">
                                                            <p className="font-weight-bold line-height-small text-center">Prima neta:</p>
                                                        </div>
                                                        <div className="col-8 pl-0 ml-0">
                                                            <input
                                                                value={
                                                                    this.state.quotation.details.premium
                                                                        ? this.state.quotation.details.premium - this.state.quotation.tax
                                                                        : this.state.quotation.amount - this.state.quotation.tax
                                                                }
                                                                className="form-control"
                                                                type="text"
                                                                readOnly
                                                                placeholder="$"
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-4 my-2">
                                                    <div className="row">
                                                        <div className="col-4 mr-0 pr-0 mt-1">
                                                            <p className="font-weight-bold line-height-small text-center">IVA:</p>
                                                        </div>
                                                        <div className="col-8 pl-0 ml-0">
                                                            <input
                                                                value={this.state.quotation.tax}
                                                                className="form-control"
                                                                type="text"
                                                                readOnly
                                                                placeholder="$"
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-4 order-5 my-2">
                                                    <div className="row">
                                                        <div className="col-4 mr-0 pr-0 mt-1">
                                                            <p className="font-weight-bold text-primary line-height-small text-center">TOTAL:</p>
                                                        </div>
                                                        <div className="col-8 pl-0 ml-0">
                                                            <input
                                                                value={this.state.quotation.amount}
                                                                className="form-control"
                                                                type="text"
                                                                readOnly
                                                                placeholder="$"
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </React.Fragment>
                                    ) : null}
                                    <button className="btn btn-primary w-25 d-block mx-auto mt-5 mb-2 font-weight-bold py-2" type="submit">
                                        {this.state.quotation ? "Continuar" : "Cotizar"}
                                    </button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
