import React from "react";
import ReactDOM from "react-dom";
import closeButton from "../Iconos/c6e7590492336f3a7ff98f5bfdc58650.svg";

export default function Modal(props) {
    if (!props.isOpen) {
        return null;
    }

    return ReactDOM.createPortal(
        <div className="row vh-100 justify-content-center align-items-center">
            <div className="col-md-10 p-0">
                <div className="card shadow border-0">
                    <div className="card-header border-0 px-0 py-0">
                        <div className={props.bgClass + " py-4 text-center"}>
                            <h4 className="my-auto">{props.title}</h4>
                            <a
                                href="/#"
                                className="btn-cerrar-popup d-block rounded-circle position-absolute border shadow-lg"
                                onClick={props.handleClose}
                            >
                                <img src={closeButton} alt="" />
                            </a>
                        </div>
                        <p className="mt-3 text-center">
                            En SIMPLI te brindamos la información detallada
                        </p>
                    </div>
                    <div className="card-body overflow-auto"></div>
                </div>
            </div>
        </div>,
        document.getElementById("modal")
    );
}
