import React, { Component } from "react";
import { Link } from "react-router-dom";
import Check from "../imagenes/check.png";

export default class External extends Component {
    componentDidMount() {
        setTimeout(() => {
            window.location.href = this.props.site;
        }, 4000);
    }

    render() {
        return (
            <div className="row justify-content-center align-items-center vh-100">
                <div className="col-5">
                    <div className="card" style={{ background: "#FFF" }}>
                        <div className="row justify-content-center">
                            <div className="col-7 text-center">
                                <img width={130} src={Check} alt="" />
                                <p className="mt-4 mb-5">
                                    Estás siendo redirigido a la pasarela de pagos. Una vez completes el pago, tu SOAT estará listo.
                                </p>
                                <Link to="/" className="btn btn-primary btn-block mb-4">
                                    Continuar
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
