import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class Navigator extends Component {
    render() {
        return (
            <div className="position-fixed" style={{ left: 0, top: "50%", transform: "translateY(-50%)", zIndex: 1000 }}>
                <div className="navigator d-flex flex-column">
                    <a className="d-flex flex-column text-center" href={process.env.REACT_APP_SIMPLI_APP_HOST}>
                        <i className="las la-user"></i>
                        <span>Mi cuenta</span>
                    </a>
                    <Link to="logout" className="d-flex flex-column text-center">
                        <i className="las la-sign-out-alt"></i>
                        <span>Salir</span>
                    </Link>
                </div>
            </div>
        );
    }
}
